.Edit-div {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 10px;
}

.Edit-area {
  width: 400px;
  height: 60px;
  border: 1px solid #d4d2d2bc;
  border-radius: 3px;
  resize: none;
  outline: none;
  padding: 5px;
  margin-top: 10px;
  text-align: left;
}

.Edit-btn {
  display: flex;
  justify-content: flex-end;
  margin-right: 15px;
}

.Twitter-main {
  border-bottom: 1px solid #d4d2d2bc;
  margin-left: 10px;
}

.Twitter-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Twitter-view {
  display: flex;
  align-items: center;
  padding-top: 13px;
}

.Twitter-title {
  margin: 20px 0;
  font-size: 13px;
  text-align: left;
}

.Twitter-nickname {
  color: #393fba;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: -0.05em;
}

.Twitter-date {
  font-size: 10px;
  margin-left: 10px;
  color: #969696bc
}

.Twitter-like {
  color:#d4d2d2bc;
  display: flex;
  justify-content: flex-end ;
  margin-bottom: 5px;
}


.Btn-icon {
  font-size: 20px;  
  color: #969696bc;
}

.Btn-heart {
  margin-right: 10px;
  font-size: 15px;
}